@use "../styles/utility";
@use "../styles/tokens";

.mkt-cmp {
  @extend .flex-v-centered;
  img {
    max-width: 50%;
  }
  //   div.title-text:nth-child(4) > div:nth-child(1) {
  //     max-width: 70%;
  //   }

  .side-by-side {
    margin-bottom: 2rem;
  }
}

.mkt-grid {
  max-width: 75%;
  height: fit-content;
}

.adv-grid {
  @extend .flex-centered;
  display: flex;
  flex-wrap: wrap;
  max-width: 50%;
  gap: 3rem;
  align-items: start;
  margin: 5rem 0;

  .grid-card {
    @extend .flex-v;
    width: 40%;
    text-align: left;

    img {
      max-width: 30%;
      padding: 1.5rem;
      padding-left: 0;
    }

    .grid-card--title {
      font-size: 1.7rem;
      font-weight: 600;
    }

    .grid-card--content {
      width: 70%;
    }
  }
}

.packages {
  @extend .flex-v-centered;
  padding: 1rem 0;
  .packages-grid {
    width: 70%;
  }

  .packages-content {
    padding: 2rem 0;
    margin: 0 20%;
  }
}

.team {
  margin: 2rem 0;

  .team-members {
    width: 70%;
  }
}

.szh-accordion__item {
  @extend .flex-v-centered;

  .szh-accordion__item-btn {
    width: 70vw;
    border-radius: 5px;
    padding: 1.2rem;
    margin: 0.25rem 0;
    text-align: left;
    font-weight: 700;
    border: 1px solid #bdbdbd;
    background-color: #fff;

    &:hover {
      cursor: pointer;
    }
  }

  .szh-accordion__item-content {
    max-width: 70%;
    text-align: center;
    text-align: left;
  }
}

.extended-footer {
  .hiw-img {
    max-width: 70%;

    &:hover {
      cursor: pointer;
    }
  }
}
